"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";
import { useState } from "react";
import InputField from "@/components/InputField";

const schema = z.object({
  name: z.string().min(1, { message: "Supplier name is required!" }),
  address: z.string().min(1, { message: "Address is required!" }),
  contact_no: z.string().min(1, { message: "Contact number is required!" }),
  manufacturing_items: z.string().min(1, { message: "Manufacturing items are required!" }),
  category: z.string().min(1, { message: "Category is required!" }),
});

type Inputs = z.infer<typeof schema>;

const SupplierForm = ({
  type,
  data,
}: {
  type: "create" | "update";
  data?: any;
}) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm<Inputs>({
    resolver: zodResolver(schema),
    defaultValues: data || {},
  });

  const [message, setMessage] = useState<string | null>(null);

  const onSubmit = handleSubmit(async (formData) => {
    setMessage(null); // Reset message

    try {
      const response = await fetch("/api/suppliers", {
        method: type === "create" ? "POST" : "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.message || "Something went wrong");
      }

      setMessage(type === "create" ? "Supplier created successfully!" : "Supplier updated successfully!");
      reset(); // Clear the form after successful submission

    } catch (error: any) {
      setMessage(error.message);
    }
  });

  // Default categories related to gold jewelry
  const categories = [
    "Gold Rings",
    "Necklaces",
    "Bracelets",
    "Earrings",
    "Gold Bars",
    "Custom Jewelry",
    "Gold Coins",
    "Jewelry Chains",
  ];

  return (
    <form className="flex flex-col gap-8" onSubmit={onSubmit}>
      <h1 className="text-xl font-semibold">
        {type === "create" ? "Create a new Supplier" : "Update Supplier"}
      </h1>

      {message && <p className="text-center text-red-500">{message}</p>}

      <div className="flex justify-between flex-wrap gap-4">
        <InputField label="Supplier Name" name="name" register={register} error={errors.name} />
        <InputField label="Address" name="address" register={register} error={errors.address} />
        <InputField label="Contact No" name="contact_no" register={register} error={errors.contact_no} />
      </div>
      <div className="flex justify-between flex-wrap gap-4">
        <InputField label="Manufacturing Items" name="manufacturing_items" register={register} error={errors.manufacturing_items} />
        <div className="flex flex-col gap-2">
          <label htmlFor="category" className="font-medium">Category</label>
          <select id="category" {...register("category")} className="border p-2 rounded-md">
            <option value="" disabled>Select a category</option>
            {categories.map((category, index) => (
              <option key={index} value={category}>{category}</option>
            ))}
          </select>
          {errors.category && <span className="text-red-500">{errors.category.message}</span>}
        </div>
      </div>

      <button className="bg-blue-400 text-white p-2 rounded-md" type="submit">
        {type === "create" ? "Create" : "Update"}
      </button>
    </form>
  );
};

export default SupplierForm;
